import React from "react";

interface Product {
  name: string;
  logo: string;
  price: string;
  rating: string;
  features: {
    speed: string;
    security: string;
    streaming: string;
    devices: string;
  };
  affiliateUrl: string;
}

const products: Product[] = [
  {
    name: "VPNSecure",
    logo: "/logos/vpnsecure.png",
    price: "$3.99/mo",
    rating: "4.8 / 5",
    features: {
      speed: "Ultra fast",
      security: "AES-256 encryption",
      streaming: "Netflix, Hulu",
      devices: "Up to 6 devices",
    },
    affiliateUrl: "https://affiliate.example.com/vpnsecure",
  },
  {
    name: "ProPrivacy VPN",
    logo: "/logos/proprivacy.png",
    price: "$4.99/mo",
    rating: "4.6 / 5",
    features: {
      speed: "Fast",
      security: "256-bit encryption",
      streaming: "Netflix, Disney+",
      devices: "Up to 5 devices",
    },
    affiliateUrl: "https://affiliate.example.com/proprivacy",
  },
  {
    name: "SafeSurf",
    logo: "/logos/safesurf.png",
    price: "$2.99/mo",
    rating: "4.4 / 5",
    features: {
      speed: "Good",
      security: "Bank-grade security",
      streaming: "Hulu, Amazon Prime",
      devices: "Up to 7 devices",
    },
    affiliateUrl: "https://affiliate.example.com/safesurf",
  },
];

const ComparisonPage: React.FC = () => {
  return (
    <div className="page-wrapper">
      <header className="header">
        <h1 className="branding">digiCompares</h1>
        <p className="subtitle">Compare the best VPN services of 2025</p>
      </header>

      <main>
        <table className="comparison-table">
          <thead>
            <tr>
              <th>Features</th>
              {products.map((product) => (
                <th key={product.name} className="product-name">
                  <img
                    src={product.logo}
                    alt={`${product.name} logo`}
                    className="product-logo"
                    loading="lazy"
                  />
                  <div>{product.name}</div>
                  <div className="product-price">{product.price}</div>
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Rating</td>
              {products.map((product) => (
                <td key={product.name}>{product.rating}</td>
              ))}
            </tr>
            <tr>
              <td>Speed</td>
              {products.map((product) => (
                <td key={product.name}>{product.features.speed}</td>
              ))}
            </tr>
            <tr>
              <td>Security</td>
              {products.map((product) => (
                <td key={product.name}>{product.features.security}</td>
              ))}
            </tr>
            <tr>
              <td>Streaming Support</td>
              {products.map((product) => (
                <td key={product.name}>{product.features.streaming}</td>
              ))}
            </tr>
            <tr>
              <td>Supported Devices</td>
              {products.map((product) => (
                <td key={product.name}>{product.features.devices}</td>
              ))}
            </tr>
            <tr className="cta-row">
              <td></td>
              {products.map((product) => (
                <td key={product.name}>
                  <a
                    href={product.affiliateUrl}
                    className="btn-cta"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Get {product.name}
                  </a>
                </td>
              ))}
            </tr>
          </tbody>
        </table>
      </main>

      <footer className="disclaimer">
        <p>
          Disclaimer: digiCompares is an independent platform and is not responsible for any purchases made through links on this website. We do not provide financial advice, and any investment or purchase decisions are solely your responsibility. Please read all terms and conditions carefully and consult a professional if needed. Use this site at your own risk.
        </p>
      </footer>

      <style jsx>{`
        .page-wrapper {
          font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
            Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
          max-width: 900px;
          margin: 40px auto;
          padding: 0 15px;
          color: #222;
        }
        .header {
          text-align: center;
          margin-bottom: 30px;
        }
        .branding {
          font-family: "Poppins", sans-serif;
          font-weight: 700;
          font-size: 3rem;
          color: #0070f3;
          margin-bottom: 5px;
          text-transform: uppercase;
          letter-spacing: 4px;
        }
        .subtitle {
          font-size: 1.2rem;
          color: #555;
        }
        .comparison-table {
          width: 100%;
          border-collapse: collapse;
          text-align: center;
        }
        .comparison-table th,
        .comparison-table td {
          border: 1px solid #ddd;
          padding: 15px 10px;
        }
        .comparison-table th {
          background: #f7faff;
          font-weight: 600;
        }
        .product-logo {
          max-height: 40px;
          margin-bottom: 8px;
        }
        .product-price {
          font-weight: 700;
          color: #0070f3;
          margin-top: 5px;
        }
        .btn-cta {
          display: inline-block;
          background: #0070f3;
          color: white;
          padding: 10px 25px;
          border-radius: 30px;
          text-decoration: none;
          font-weight: 700;
          transition: background 0.3s ease;
        }
        .btn-cta:hover {
          background: #005bb5;
        }
        .disclaimer {
          margin-top: 40px;
          font-size: 0.8rem;
          color: #777;
          text-align: center;
          padding: 20px 10px;
          border-top: 1px solid #ddd;
        }
        @media (max-width: 700px) {
          .comparison-table,
          .comparison-table thead,
          .comparison-table tbody,
          .comparison-table th,
          .comparison-table td,
          .comparison-table tr {
            display: block;
          }
          .comparison-table thead tr {
            position: absolute;
            top: -9999px;
            left: -9999px;
          }
          .comparison-table tr {
            margin-bottom: 20px;
            border-bottom: 2px solid #ddd;
          }
          .comparison-table td {
            border: none;
            position: relative;
            padding-left: 50%;
            text-align: left;
          }
          .comparison-table td:before {
            position: absolute;
            top: 15px;
            left: 15px;
            width: 45%;
            white-space: nowrap;
            font-weight: 600;
            content: attr(data-label);
          }
          .btn-cta {
            width: 100%;
            box-sizing: border-box;
            text-align: center;
          }
        }
      `}</style>
    </div>
  );
};

export default ComparisonPage;
